import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage1 from '../assets/images/intro/bg1.jpg'
import backgroundImage2 from '../assets/images/intro/bg2.jpg'
import backgroundImage3 from '../assets/images/intro/bg3.jpg'
import learnSpaceBtn from '../assets/images/intro/study_btn.png'
import completedEndBtn from '../assets/images/intro/end.png'
import continueBtn from '../assets/images/intro/continue.png';

import constant from '../assets/const.json';

class Intro extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            currentIndex: 0,
            backgroundImage: backgroundImage1
        };
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    goToHomePage() {
        this.playBtnSound();
        if (this.state.currentIndex >= 2) {
            this.props.history.push({
                pathname: './',
                state: {}
            });
        } else {
            let index = this.state.currentIndex;
            this.setState({
                backgroundImage: index === 0 ? backgroundImage2 : backgroundImage3,
                currentIndex: index + 1
            });
        }
    }

    goToStudyAreaPage() {
        this.playBtnSound();
        window.top.location = constant.STUDY_AREA_URL
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img alt='' className='press-btn' src={learnSpaceBtn} style={{ position: 'absolute', height: '10%', right: '57%', bottom: '5.8%' }} onClick={this.goToStudyAreaPage.bind(this)}/>
                <img alt='' className='press-btn' src={this.state.currentIndex >= 2 ? completedEndBtn : continueBtn} style={{ position: 'absolute', height: '10%', left: '59%', bottom: '5.8%' }} onClick={this.goToHomePage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Intro);