import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/desc3/bg.jpg'
import nextBtn from '../assets/images/continue.png';

class Desc3 extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
    }
    
    goToGamePage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './game3',
            state: {
                gameScore1: this.props.location.state.gameScore1,
                gameScore2: this.props.location.state.gameScore2
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                <img className='press-btn' alt='' src={nextBtn} style={{ height: '11.5%', bottom: '5%', left: '41%', position: 'absolute', background: 'transparent' }} onClick={this.goToGamePage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Desc3);