import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/game1/bg.jpg'
import card1Selected from '../assets/images/game1/card01_selected.png';
import card2Selected from '../assets/images/game1/card02_selected.png';
import card3Selected from '../assets/images/game1/card03_selected.png';
import card4Selected from '../assets/images/game1/card04_selected.png';
import card5Selected from '../assets/images/game1/card05_selected.png';
import card6Selected from '../assets/images/game1/card06_selected.png';
import card1 from '../assets/images/game1/card01.png';
import card2 from '../assets/images/game1/card02.png';
import card3 from '../assets/images/game1/card03.png';
import card4 from '../assets/images/game1/card04.png';
import card5 from '../assets/images/game1/card05.png';
import card6 from '../assets/images/game1/card06.png';
import hintBtn from '../assets/images/game1/hint_btn.png';
import hintPanel from '../assets/images/game1/hint_panel.png';
import closeHintBtn from '../assets/images/game1/close_hint.png';
import add10 from '../assets/images/add.png';
import remove2 from '../assets/images/remove.png';

class Game1 extends Component {

    btnSound = './sounds/button_sound.mp3';
    btnRemoveScoreSound = './sounds/remove_score.mp3';
    btnAddScoreSound = './sounds/add_score.mp3';
    btnCorrectSound = './sounds/game1_correct.mp3';
    btnHintSound = './sounds/hint_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            countdown: 30,
            score: 0,
            playing: false,
            gameOverTimeout: false,
            gameCompleted: false,
            hint: false,
            selectedIndex: [],
            position1: 0,
            position2: 3,
            position3: 5,
            position4: 1,
            position5: 2,
            position6: 4,
            position7: 4,
            position8: 2,
            position9: 1,
            position10: 3,
            position11: 0,
            position12: 5,
            add10Opacity: 0,
            remove2Opacity: 0,
        };
    }

    componentDidMount() {
        this.setState({
            playing: true
        });
        this.countdownInterval = setInterval(() => {
            if (this.state.countdown === 0) {
                this.setState({
                    gameOverTimeout: true,
                    playing: false
                });
                setTimeout(() => {
                    this.goToResultPage();
                }, 1000);
            } else {
                this.setState({
                    countdown: this.state.countdown - 1
                });
            }
        }, 1 * 1000);
    }

    componentWillUnmount() {
        this.setState({ playing : false });
        clearInterval(this.countdownInterval);
    }

    playHintSound() {
        let btnAudio = new Audio(this.btnHintSound);
        btnAudio.play();
    }

    playAddScoreSound() {
        let btnAudio = new Audio(this.btnAddScoreSound);
        btnAudio.play();
    }

    playRemoveScoreSound() {
        let btnAudio = new Audio(this.btnRemoveScoreSound);
        btnAudio.play();
    }

    playCorrectSound() {
        let btnAudio = new Audio(this.btnCorrectSound);
        btnAudio.play();
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    showHint() {
        this.playHintSound();
        this.setState({
            hint: true
        });
    }

    closeHint() {
        this.playBtnSound();
        this.setState({
            hint: false
        });
    }

    goToResultPage() {
        this.props.history.push({
            pathname: './game1_result',
            state: {
                gameScore1: this.state.score
            }
        });
    }

    getIndexFromPosition(position) {
        return this.state['position' + (position + 1)]
    }

    checkMatch(indexArray) {
        if (indexArray.length === 2) {
            let index1 = this.getIndexFromPosition(indexArray[0])
            let index2 = this.getIndexFromPosition(indexArray[1])
            let sum = index1 + index2
            if (sum === 1 || sum === 5 || sum === 9) {
                return true;
            } else {
                return false;
            }
        } else {
            return undefined;
        }
    }

    randomIndex() {
        return Math.floor(Math.random() * 5);
    }

    clickOnIndex(position) {
        this.playBtnSound();
        let indexes = this.state.selectedIndex;
        indexes.push(position);
        if (indexes.length > 2) {
            indexes.splice(0, 1);
        }
        this.setState({
            selectedIndex: indexes
        });
        setTimeout(() => {
            let match = this.checkMatch(indexes);
            if (match != undefined) {
                if (match) {
                    this.playAddScoreSound();
                    this.setState({
                        selectedIndex: [],
                        add10Opacity: 1,
                        score: this.state.score + 10
                    });
                    setTimeout(() => {
                        this.setState({
                            ['position' + (indexes[0] + 1)]: this.randomIndex(),
                            ['position' + (indexes[1] + 1)]: this.randomIndex(),
                            add10Opacity: 0
                        });
                        this.playCorrectSound();
                    }, 500);
                } else {
                    this.playRemoveScoreSound();
                    if (this.state.score - 2 >= 0) {
                        this.setState({
                            remove2Opacity: 1,
                            score: this.state.score - 2,
                            selectedIndex: []
                        });
                        setTimeout(() => {
                            this.setState({
                                remove2Opacity: 0
                            });
                        }, 500);
                    }
                }
            }
        }, 500);
    }

    getCard(index, position) {
        if (index === 0) {
            return this.state.selectedIndex.includes(position) ? card1Selected : card1;
        } else if (index === 1) {
            return this.state.selectedIndex.includes(position) ? card2Selected : card2;
        } else if (index === 2) {
            return this.state.selectedIndex.includes(position) ? card3Selected : card3;
        } else if (index === 3) {
            return this.state.selectedIndex.includes(position) ? card4Selected : card4;
        } else if (index === 4) {
            return this.state.selectedIndex.includes(position) ? card5Selected : card5;
        } else if (index === 5) {
            return this.state.selectedIndex.includes(position) ? card6Selected : card6;
        }
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <div style={{ position: 'absolute', right: '3.1%', top: '6.3%', width: '11%', height: '8%', color: '#086940' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#ffffff" >{this.state.countdown}</text>
                    </svg>
                </div>
                <div style={{ position: 'absolute', left: '7.1%', top: '6.1%', width: '11%', height: '8%', color: '#086940' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#ffffff" >{this.state.score}</text>
                    </svg>
                </div>
                <div>
                    <img alt='' src={this.getCard(this.state.position1, 0)} style={{ position: 'absolute', width: '14%', left: '6.3%', top: '18.2%' }} onClick={ () => this.clickOnIndex(0) }></img>
                    <img alt='' src={this.getCard(this.state.position2, 1)} style={{ position: 'absolute', width: '14%', left: '21.0%', top: '18.2%' }} onClick={ () => this.clickOnIndex(1) }></img>
                    <img alt='' src={this.getCard(this.state.position3, 2)} style={{ position: 'absolute', width: '14%', left: '35.8%', top: '18.2%' }} onClick={ () => this.clickOnIndex(2) }></img>
                    <img alt='' src={this.getCard(this.state.position4, 3)} style={{ position: 'absolute', width: '14%', left: '50.6%', top: '18.2%' }} onClick={ () => this.clickOnIndex(3) }></img>
                    <img alt='' src={this.getCard(this.state.position5, 4)} style={{ position: 'absolute', width: '14%', left: '65.4%', top: '18.2%' }} onClick={ () => this.clickOnIndex(4) }></img>
                    <img alt='' src={this.getCard(this.state.position6, 5)} style={{ position: 'absolute', width: '14%', left: '80%', top: '18.2%' }} onClick={ () => this.clickOnIndex(5) }></img>
                    <img alt='' src={this.getCard(this.state.position7, 6)} style={{ position: 'absolute', width: '14%', left: '6.3%', top: '53.2%' }} onClick={ () => this.clickOnIndex(6) }></img>
                    <img alt='' src={this.getCard(this.state.position8, 7)} style={{ position: 'absolute', width: '14%', left: '21.0%', top: '53.2%' }} onClick={ () => this.clickOnIndex(7) }></img>
                    <img alt='' src={this.getCard(this.state.position9, 8)} style={{ position: 'absolute', width: '14%', left: '35.8%', top: '53.2%' }} onClick={ () => this.clickOnIndex(8) }></img>
                    <img alt='' src={this.getCard(this.state.position10, 9)} style={{ position: 'absolute', width: '14%', left: '50.6%', top: '53.2%' }} onClick={ () => this.clickOnIndex(9) }></img>
                    <img alt='' src={this.getCard(this.state.position11, 10)} style={{ position: 'absolute', width: '14%', left: '65.4%', top: '53.2%' }} onClick={ () => this.clickOnIndex(10) }></img>
                    <img alt='' src={this.getCard(this.state.position12, 11)} style={{ position: 'absolute', width: '14%', left: '80%', top: '53.2%' }} onClick={ () => this.clickOnIndex(11) }></img>
                </div>
                <img alt='' src={add10} style={{ position: 'absolute', width: '9.4%', left: '8.7%', top: '12.2%', opacity: this.state.add10Opacity }}></img>
                <img alt='' src={remove2} style={{ position: 'absolute', width: '6.4%', left: '9.7%', top: '12.2%', opacity: this.state.remove2Opacity }}></img>
                {
                    this.state.hint && (<div style={{ height: '70.5%', width: '57%', bottom: '7%', left: '21.5%', position: 'absolute', background: 'transparent', zIndex: '1' }}>
                        <img alt='' src={hintPanel} style={{ position: 'absolute', width: '100%', left: '0%', top: '0%' }}></img>
                        <img alt='' src={closeHintBtn} style={{ position: 'absolute', width: '10%', right: '-5%', top: '-5%' }} onClick={this.closeHint.bind(this)}></img>
                    </div>)
                }
                <img className='press-btn' alt='' src={hintBtn} style={{ height: '10%', bottom: '1%', right: '1%', position: 'absolute', background: 'transparent' }} onClick={this.showHint.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Game1);