import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage1 from '../assets/images/desc1_2/bg1.jpg'
import backgroundImage2 from '../assets/images/desc1_2/bg2.jpg'
import backgroundImage3 from '../assets/images/desc1_2/bg3.jpg'
import startBtn from '../assets/images/start.png';
import nextBtn from '../assets/images/next_page.png';
import previousBtn from '../assets/images/previous_page.png';

class Desc1_2 extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            index: 0,
            backgroundImage: backgroundImage1
        }
    }

    previous() {
        this.playBtnSound();
        if (this.state.index === 1) {
            this.setState({
                index: 0,
                backgroundImage: backgroundImage1
            });
        } else if (this.state.index === 2) {
            this.setState({
                index: 0,
                backgroundImage: backgroundImage2
            });
        } else {
            this.goBackPage();
        }
    }

    next() {
        if (this.state.index === 0) {
            this.playBtnSound();
            this.setState({
                index: 1,
                backgroundImage: backgroundImage2
            });
        } else if (this.state.index === 1) {
            this.playBtnSound();
            this.setState({
                index: 2,
                backgroundImage: backgroundImage3
            });
        } else {
            this.goToGame1();
        }
    }

    goBackPage() {
        this.props.history.goBack();
    }
    
    goToGame1() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './game1',
            state: {}
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <img className='press-btn' alt='' src={previousBtn} style={{height: '9.5%', bottom: '11%', left: '31%', position: 'absolute', background: 'transparent' }} onClick={this.previous.bind(this)}></img>
                <img className='press-btn' alt='' src={this.state.index === 2 ? startBtn : nextBtn} style={{ height: '9.5%', bottom: '11%', right: '17%', position: 'absolute', background: 'transparent' }} onClick={this.next.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Desc1_2);