class Game3 extends Component {

    btnSound = './sounds/button_sound.mp3';
    furnitureSound = './sounds/furniture_sound.mp3';
    currentIndex = 0;

    constructor(props) {
        super(props);
        this.state = {
            dropRoomA: {},
            dropRoomB: {},
            dropRoomC: {},
            dropRoomD: {},
            showRoomA: false,
            showRoomB: false,
            showRoomC: false,
            showRoomD: false,
            furniture: bathroomFurniture,
            item1Index: 0,
            item2Index: 0,
            item3Index: 0,
            item4Index: 0,
            currentSelectIndex: 0,
            firstImage: bathroomFurniture[0][0],
            secondImage: bathroomFurniture[1][0],
            thirdImage: bathroomFurniture[2][0],
            forthImage: bathroomFurniture[3][0],
            floorSelected: false,
            floorRoomA: null,
            floorRoomB: null,
            floorRoomC: null,
            floorRoomD: null
        };
    }

    isInRoom(target, room) {
        let percentage = 0.5;
        let targetTop = target.top + target.height * percentage;
        let targetBottom = target.bottom - target.height * percentage;
        let targetLeft = target.left + target.width * percentage;
        let targetRight = target.right - target.width * percentage;
        return (
            targetTop >= room.top && targetBottom <= room.bottom && targetLeft >= room.left && targetRight <= room.right
            // ((room.top <= targetTop) && (targetTop <= room.bottom)) &&
            // ((room.top <= targetBottom) && (targetBottom <= room.bottom)) &&
            // ((room.left <= targetLeft) && (targetLeft <= room.right)) &&
            // ((room.left <= targetRight) && (targetRight <= room.right))
        );
    }

    onDrag(e) {
        let bounds = e.target.getBoundingClientRect();
        if (this.isInRoom(bounds, this.state.dropRoomA)) {
            if (!this.state.showRoomA) {
                this.setState({
                    showRoomA: true,
                    showRoomB: false,
                    showRoomC: false,
                    showRoomD: false
                });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomB)) {
            if (!this.state.showRoomB) {
                this.setState({
                    showRoomA: false,
                    showRoomB: true,
                    showRoomC: false,
                    showRoomD: false
                });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomC)) {
            if (!this.state.showRoomC) {
                this.setState({
                    showRoomA: false,
                    showRoomB: false,
                    showRoomC: true,
                    showRoomD: false
                });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomD)) {
            if (!this.state.showRoomD) {
                this.setState({
                    showRoomA: false,
                    showRoomB: false,
                    showRoomC: false,
                    showRoomD: true
                });
            }
        } else {
            if (this.state.showRoomA || this.state.showRoomB || this.state.showRoomC || this.state.showRoomD) {
                this.setState({
                    showRoomA: false,
                    showRoomB: false,
                    showRoomC: false,
                    showRoomD: false
                });
            }
        }
    }

    getMatrix(element) {
        const transform = element.style.transform;
        const re = /translate3d\((?<x>.*?)px, (?<y>.*?)px, (?<z>.*?)px/
        const results = re.exec(transform);
    
        return {
          x: results.groups.x,
          y: results.groups.y,
          z: results.groups.z
        };
    }

    onRelease(e) {
        let bounds = e.target.getBoundingClientRect();
        let matrix = this.getMatrix(e.target);
        let name = e.target.className;
        if (this.isInRoom(bounds, this.state.dropRoomA)) {
            this.playDropSound();
            let img = e.target.cloneNode();
            img.className = 'clone'
            const originX = document.createAttribute("originX");
            const originY = document.createAttribute("originY");
            originX.value = matrix.x;
            originY.value = matrix.y;
            img.setAttributeNode(originX);
            img.setAttributeNode(originY);
            if (name === 'furniture1') {
                this.refs.furnitureContainer1.append(img)
            } else if (name === 'furniture2') {
                this.refs.furnitureContainer2.append(img)
            } else if (name === 'furniture3') {
                this.refs.furnitureContainer3.append(img)
            } else if (name === 'furniture4') {
                this.refs.furnitureContainer4.append(img)
            }
            tl.to(e.target, 0.1, { x: 0, y: 0 });
            Draggable.create(img, {
                onDrag: this.onDrag.bind(this),
                onRelease: this.onReleaseClone.bind(this)
            })
        } else if (this.isInRoom(bounds, this.state.dropRoomB)) {
            this.playDropSound();
            let img = e.target.cloneNode();
            img.className = 'clone'
            const originX = document.createAttribute("originX");
            const originY = document.createAttribute("originY");
            originX.value = matrix.x;
            originY.value = matrix.y;
            img.setAttributeNode(originX);
            img.setAttributeNode(originY);
            if (name === 'furniture1') {
                this.refs.furnitureContainer1.append(img)
            } else if (name === 'furniture2') {
                this.refs.furnitureContainer2.append(img)
            } else if (name === 'furniture3') {
                this.refs.furnitureContainer3.append(img)
            } else if (name === 'furniture4') {
                this.refs.furnitureContainer4.append(img)
            }
            tl.to(e.target, 0.1, { x: 0, y: 0 });
            Draggable.create(img, {
                onDrag: this.onDrag.bind(this),
                onRelease: this.onReleaseClone.bind(this)
            })
        } else if (this.isInRoom(bounds, this.state.dropRoomC)) {
            this.playDropSound();
            let img = e.target.cloneNode();
            img.className = 'clone'
            const originX = document.createAttribute("originX");
            const originY = document.createAttribute("originY");
            originX.value = matrix.x;
            originY.value = matrix.y;
            img.setAttributeNode(originX);
            img.setAttributeNode(originY);
            if (name === 'furniture1') {
                this.refs.furnitureContainer1.append(img)
            } else if (name === 'furniture2') {
                this.refs.furnitureContainer2.append(img)
            } else if (name === 'furniture3') {
                this.refs.furnitureContainer3.append(img)
            } else if (name === 'furniture4') {
                this.refs.furnitureContainer4.append(img)
            }
            tl.to(e.target, 0.1, { x: 0, y: 0 });
            Draggable.create(img, {
                onDrag: this.onDrag.bind(this),
                onRelease: this.onReleaseClone.bind(this)
            })
        } else if (this.isInRoom(bounds, this.state.dropRoomD)) {
            this.playDropSound();
            let img = e.target.cloneNode();
            img.className = 'clone'
            const originX = document.createAttribute("originX");
            const originY = document.createAttribute("originY");
            originX.value = matrix.x;
            originY.value = matrix.y;
            img.setAttributeNode(originX);
            img.setAttributeNode(originY);
            if (name === 'furniture1') {
                this.refs.furnitureContainer1.append(img)
            } else if (name === 'furniture2') {
                this.refs.furnitureContainer2.append(img)
            } else if (name === 'furniture3') {
                this.refs.furnitureContainer3.append(img)
            } else if (name === 'furniture4') {
                this.refs.furnitureContainer4.append(img)
            }
            tl.to(e.target, 0.1, { x: 0, y: 0 });
            Draggable.create(img, {
                onDrag: this.onDrag.bind(this),
                onRelease: this.onReleaseClone.bind(this)
            })
        } else {
            tl.to(e.target, 0.3, { x: 0, y: 0 });
        }
        this.setState({
            showRoomA: false,
            showRoomB: false,
            showRoomC: false,
            showRoomD: false
        });
    }

    onReleaseClone(e) {
        let bounds = e.target.getBoundingClientRect();
        if (this.isInRoom(bounds, this.state.dropRoomA)) {
            this.playDropSound();
        } else if (this.isInRoom(bounds, this.state.dropRoomB)) {
            this.playDropSound();
        } else if (this.isInRoom(bounds, this.state.dropRoomC)) {
            this.playDropSound();
        } else if (this.isInRoom(bounds, this.state.dropRoomD)) {
            this.playDropSound();
        } else {
            tl.to(e.target, 0.3, { x: e.target.getAttribute('originX'), y: e.target.getAttribute('originY') });
        }
        this.setState({
            showRoomA: false,
            showRoomB: false,
            showRoomC: false,
            showRoomD: false
        });
    }

    onReleaseFloor(e) {
        let bounds = e.target.getBoundingClientRect();
        if (this.isInRoom(bounds, this.state.dropRoomA)) {
            this.playDropSound();
            if (e.target.className.includes('blue')) {
                this.setState({ floorRoomA: blueFloorA });
            } else if (e.target.className.includes('gray')) {
                this.setState({ floorRoomA: grayFloorA });
            } else if (e.target.className.includes('marble')) {
                this.setState({ floorRoomA: marbleFloorA });
            } else if (e.target.className.includes('tile')) {
                this.setState({ floorRoomA: tileFloorA });
            } else if (e.target.className.includes('wood')) {
                this.setState({ floorRoomA: woodFloorA });
            } else if (e.target.className.includes('none')) {
                this.setState({ floorRoomA: null });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomB)) {
            this.playDropSound();
            if (e.target.className.includes('blue')) {
                this.setState({ floorRoomB: blueFloorB });
            } else if (e.target.className.includes('gray')) {
                this.setState({ floorRoomB: grayFloorB });
            } else if (e.target.className.includes('marble')) {
                this.setState({ floorRoomB: marbleFloorB });
            } else if (e.target.className.includes('tile')) {
                this.setState({ floorRoomB: tileFloorB });
            } else if (e.target.className.includes('wood')) {
                this.setState({ floorRoomB: woodFloorB });
            } else if (e.target.className.includes('none')) {
                this.setState({ floorRoomB: null });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomC)) {
            this.playDropSound();
            if (e.target.className.includes('blue')) {
                this.setState({ floorRoomC: blueFloorC });
            } else if (e.target.className.includes('gray')) {
                this.setState({ floorRoomC: grayFloorC });
            } else if (e.target.className.includes('marble')) {
                this.setState({ floorRoomC: marbleFloorC });
            } else if (e.target.className.includes('tile')) {
                this.setState({ floorRoomC: tileFloorC });
            } else if (e.target.className.includes('wood')) {
                this.setState({ floorRoomC: woodFloorC });
            } else if (e.target.className.includes('none')) {
                this.setState({ floorRoomC: null });
            }
        } else if (this.isInRoom(bounds, this.state.dropRoomD)) {
            this.playDropSound();
            if (e.target.className.includes('blue')) {
                this.setState({ floorRoomD: blueFloorD });
            } else if (e.target.className.includes('gray')) {
                this.setState({ floorRoomD: grayFloorD });
            } else if (e.target.className.includes('marble')) {
                this.setState({ floorRoomD: marbleFloorD });
            } else if (e.target.className.includes('tile')) {
                this.setState({ floorRoomD: tileFloorD });
            } else if (e.target.className.includes('wood')) {
                this.setState({ floorRoomD: woodFloorD });
            } else if (e.target.className.includes('none')) {
                this.setState({ floorRoomD: null });
            }
        }
        tl.to(e.target, 0.3, { x: 0, y: 0 });
        this.setState({
            showRoomA: false,
            showRoomB: false,
            showRoomC: false,
            showRoomD: false
        });
    }

    componentDidMount() {
        let roomA = this.refs.dropRoomA;
        let roomABounds = roomA.getBoundingClientRect();
        let roomB = this.refs.dropRoomB;
        let roomBBounds = roomB.getBoundingClientRect();
        let roomC = this.refs.dropRoomC;
        let roomCBounds = roomC.getBoundingClientRect();
        let roomD = this.refs.dropRoomD;
        let roomDBounds = roomD.getBoundingClientRect();
        this.setState({
            dropRoomA: {
                left: roomABounds.left,
                top: roomABounds.top,
                bottom: roomABounds.bottom,
                right: roomABounds.right,
                x: roomABounds.x,
                y: roomABounds.y
            },
            dropRoomB: {
                left: roomBBounds.left,
                top: roomBBounds.top,
                bottom: roomBBounds.bottom,
                right: roomBBounds.right,
                x: roomBBounds.x,
                y: roomBBounds.y
            },
            dropRoomC: {
                left: roomCBounds.left,
                top: roomCBounds.top,
                bottom: roomCBounds.bottom,
                right: roomCBounds.right,
                x: roomCBounds.x,
                y: roomCBounds.y
            },
            dropRoomD: {
                left: roomDBounds.left,
                top: roomDBounds.top,
                bottom: roomDBounds.bottom,
                right: roomDBounds.right,
                x: roomDBounds.x,
                y: roomDBounds.y
            }
        });
        Draggable.create('.furniture1', {
            onDrag: this.onDrag.bind(this),
            onRelease: this.onRelease.bind(this)
        });
        Draggable.create('.furniture2', {
            onDrag: this.onDrag.bind(this),
            onRelease: this.onRelease.bind(this)
        });
        Draggable.create('.furniture3', {
            onDrag: this.onDrag.bind(this),
            onRelease: this.onRelease.bind(this)
        });
        Draggable.create('.furniture4', {
            onDrag: this.onDrag.bind(this),
            onRelease: this.onRelease.bind(this)
        });
        Draggable.create('.floor', {
            onDrag: this.onDrag.bind(this),
            onRelease: this.onReleaseFloor.bind(this)
        });
    }

    previousImage(index) {
        this.playBtnSound();
        if (index === 0) {
            let currentIndex = this.state.item1Index;
            if (currentIndex - 1 < 0) {
                currentIndex = this.state.furniture[0].length - 1;
            } else {
                currentIndex -= 1;
            }
            this.setState({
                item1Index: currentIndex,
                firstImage: this.state.furniture[0][currentIndex]
            });
        } else if (index === 1) {
            let currentIndex = this.state.item2Index;
            if (currentIndex - 1 < 0) {
                currentIndex = this.state.furniture[1].length - 1;
            } else {
                currentIndex -= 1;
            }
            this.setState({
                item2Index: currentIndex,
                secondImage: this.state.furniture[1][currentIndex]
            });
        } else if (index === 2) {
            let currentIndex = this.state.item3Index;
            if (currentIndex - 1 < 0) {
                currentIndex = this.state.furniture[2].length - 1;
            } else {
                currentIndex -= 1;
            }
            this.setState({
                item3Index: currentIndex,
                thirdImage: this.state.furniture[2][currentIndex]
            });
        } else if (index === 3) {
            let currentIndex = this.state.item4Index;
            if (currentIndex - 1 < 0) {
                currentIndex = this.state.furniture[3].length - 1;
            } else {
                currentIndex -= 1;
            }
            this.setState({
                item4Index: currentIndex,
                forthImage: this.state.furniture[3][currentIndex]
            });
        }
    }

    nextImage(index) {
        this.playBtnSound();
        if (index === 0) {
            let currentIndex = this.state.item1Index;
            if (currentIndex + 1 >= this.state.furniture[0].length) {
                currentIndex = 0;
            } else {
                currentIndex += 1;
            }
            this.setState({
                item1Index: currentIndex,
                firstImage: this.state.furniture[0][currentIndex]
            });
        } else if (index === 1) {
            let currentIndex = this.state.item2Index;
            if (currentIndex + 1 >= this.state.furniture[1].length) {
                currentIndex = 0;
            } else {
                currentIndex += 1;
            }
            this.setState({
                item2Index: currentIndex,
                secondImage: this.state.furniture[1][currentIndex]
            });
        } else if (index === 2) {
            let currentIndex = this.state.item3Index;
            if (currentIndex + 1 >= this.state.furniture[2].length) {
                currentIndex = 0;
            } else {
                currentIndex += 1;
            }
            this.setState({
                item3Index: currentIndex,
                thirdImage: this.state.furniture[2][currentIndex]
            });
        } else if (index === 3) {
            let currentIndex = this.state.item4Index;
            if (currentIndex + 1 >= this.state.furniture[3].length) {
                currentIndex = 0;
            } else {
                currentIndex += 1;
            }
            this.setState({
                item4Index: currentIndex,
                forthImage: this.state.furniture[3][currentIndex]
            });
        }
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    playDropSound() {
        let btnAudio = new Audio(this.furnitureSound);
        btnAudio.play();
    }

    goToResultPage(image, component) {
        component.cropImage(image, 607, 570, 90, 95, component, function(uri, cpt) {
            cpt.props.history.push({
                pathname: './result',
                state: {
                    image: uri,
                    gameScore1: cpt.props.location.state === undefined ? '0' : cpt.props.location.state.gameScore1,
                    gameScore2: cpt.props.location.state === undefined ? '0' : cpt.props.location.state.gameScore2
                }
            });
        });
    }

    cropImage(imgUri, width, height, xstart, ystart, component, callback) {
        try {
            let resize_canvas = document.createElement('canvas');
            let orig_src = new Image();
            orig_src.src = imgUri;
            orig_src.onload = function () {
                resize_canvas.width = width;
                resize_canvas.height = height;
                let cnv = resize_canvas.getContext('2d');
                cnv.drawImage(orig_src, xstart, ystart, width, height, 0, 0, width, height);
                let newimgUri = resize_canvas.toDataURL("image/png").toString();
                callback(newimgUri, component);
            }
        }
        catch (e) {
            console.log("Couldn't crop image due to", e);
            callback(imgUri, component);
        }
    }

    createScreenshot(goToResultPage) {
        this.playBtnSound();
        let component = this;
        let node = document.getElementById('result-div');
        let scale = constant.DESIRED_RATIO_WIDTH / node.clientWidth;
        domtoimage.toPng(node, {
            width: constant.DESIRED_RATIO_WIDTH,
            height: constant.DESIRED_RATIO_HEIGHT,
            style: {
                transform: 'scale(' + scale + ')',
                transformOrigin: 'top left'
            }
        })
        // crop -> 90, 90, 610, 580
        .then(function (dataUrl) {
            goToResultPage(dataUrl, component);
        })
        .catch(function (error) {
            console.error('oops, something went wrong!', error);
        });
    }

    menuBtnClick(index) {
        this.playBtnSound();
        let furniture = index === 0 ? bathroomFurniture : (index === 1 ? livingroomFurniture : (index === 2 ? bedroomFurniture : (index === 3 ? kitchenFurniture : [])))
        this.setState({
            currentSelectIndex: index,
            furniture: furniture,
            firstImage: index === 4 ? null : furniture[0][0],
            secondImage: index === 4 ? null : furniture[1][0],
            thirdImage: index === 4 ? null : furniture[2][0],
            forthImage: index === 4 ? null : furniture[3][0],
            item1Index: 0,
            item2Index: 0,
            item3Index: 0,
            item4Index: 0
        });
    }

    render() {
        return (
            <div className='page-root-div'>
                <div id='result-div' style={{ position: 'absolute', width: '100%', height: '100%', left: '0px', top: '0px' }}>
                    <img className='undrag'  alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                    <div id='room-canvas' ref='dropContainer' style={{ width: '47.5%', height: '79%', left: '7%', top: '13%', position: 'absolute', background: 'white' }}>
                        
                        {/* <img className='undrag' alt='' src={roomRed} style={{ width: '100%', left: '0%', top: '0%', position: 'absolute' }}/> */}
                        <div ref='dropRoomD' style={{ width: '100%', height: '64%', left: '0%', bottom: '0%', position: 'absolute' }}>
                            <img alt='' src={this.state.floorRoomD} style={{ position: 'absolute', width: '100%', height: '100%', opacity: this.state.floorRoomD === null ? 0 : 1 }}></img>
                            <img alt='' src={roomSelectD} style={{ position: 'absolute', width: '100%', left: '0%', top: '0%', opacity: this.state.showRoomD ? '1' : '0' }}/>
                        </div>
                        <div ref='dropRoomA' style={{ width: '33%', height: '53%', left: '0%', top: '0%', position: 'absolute' }}>
                            <img alt='' src={this.state.floorRoomA} style={{ position: 'absolute', width: '100%', height: '100%', opacity: this.state.floorRoomA === null ? 0 : 1 }}></img>
                            <img className='undrag' alt='' src={roomSelectA} style={{ position: 'absolute', width: '100%', left: '0%', top: '0%', opacity: this.state.showRoomA ? '1' : '0' }}/>
                        </div>
                        <div ref='dropRoomB' style={{ width: '28%', height: '35%', left: '33%', top: '0%', position: 'absolute' }}>
                            <img alt='' src={this.state.floorRoomB} style={{ position: 'absolute', width: '100%', height: '100%', opacity: this.state.floorRoomB === null ? 0 : 1 }}></img>
                            <img className='undrag' alt='' src={roomSelectB} style={{ position: 'absolute', width: '100%', left: '0%', top: '0%', opacity: this.state.showRoomB ? '1' : '0' }}/>
                        </div>
                        <div ref='dropRoomC' style={{ width: '39%', height: '53%', right: '0%', top: '0%', position: 'absolute' }}>
                            <img alt='' src={this.state.floorRoomC} style={{ position: 'absolute', width: '100%', height: '100%', opacity: this.state.floorRoomC === null ? 0 : 1 }}></img>
                            <img className='undrag' alt='' src={roomSelectC} style={{ position: 'absolute', width: '100%', left: '0%', top: '0%', opacity: this.state.showRoomC ? '1' : '0' }}/>
                        </div>
                        <img className='undrag' alt='' src={roomBlack} style={{ width: '100%', left: '0%', top: '0%', position: 'absolute' }}/>
                    </div>
                    <div style={{ width: '20.5%', height: '61.5%', left: '61.6%', top: '17%', position: 'absolute', zIndex: this.state.currentSelectIndex != 4 ? '2' : '1' }}>
                        <img className='undrag' alt='' src={menuPanel} style={{ width: '100%', left: '0%', top: '0%', position: 'absolute' }}></img>
                        <img className='undrag' alt='' src={arrowLeft} style={{ width: '10%', left: '5%', top: '8%', position: 'absolute' }} onClick={() => { this.previousImage(0) }}></img>
                        <img className='undrag' alt='' src={arrowLeft} style={{ width: '10%', left: '5%', top: '33%', position: 'absolute' }} onClick={() => { this.previousImage(1) }}></img>
                        <img className='undrag' alt='' src={arrowLeft} style={{ width: '10%', left: '5%', top: '58%', position: 'absolute' }} onClick={() => { this.previousImage(2) }}></img>
                        <img className='undrag' alt='' src={arrowLeft} style={{ width: '10%', left: '5%', top: '83%', position: 'absolute' }} onClick={() => { this.previousImage(3) }}></img>
                        <img className='undrag' alt='' src={arrowRight} style={{ width: '10%', right: '5%', top: '8%', position: 'absolute' }} onClick={() => { this.nextImage(0) }}></img>
                        <img className='undrag' alt='' src={arrowRight} style={{ width: '10%', right: '5%', top: '33%', position: 'absolute' }} onClick={() => { this.nextImage(1) }}></img>
                        <img className='undrag' alt='' src={arrowRight} style={{ width: '10%', right: '5%', top: '58%', position: 'absolute' }} onClick={() => { this.nextImage(2) }}></img>
                        <img className='undrag' alt='' src={arrowRight} style={{ width: '10%', right: '5%', top: '83%', position: 'absolute' }} onClick={() => { this.nextImage(3) }}></img>
                            
                        <div ref='furnitureContainer1' style={{ position: 'absolute', width: '50%', height: '18%', left: '25%', top: '4%', background: 'transparent'}}>
                            <img className='furniture1' src={this.state.firstImage} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain', clear: 'both', content: '', display:'block' }}></img>
                        </div>
                        <div ref='furnitureContainer2' style={{ position: 'absolute', width: '50%', height: '18%', left: '25%', top: '28%', background: 'transparent' }}>
                            <img className='furniture2' src={this.state.secondImage} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }}></img>
                        </div>
                        <div ref='furnitureContainer3' style={{ position: 'absolute', width: '50%', height: '18%', left: '25%', top: '53%', background: 'transparent' }}>
                            <img className='furniture3' src={this.state.thirdImage} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                        <div ref='furnitureContainer4' style={{ position: 'absolute', width: '50%', height: '18%', left: '25%', top: '78%', background: 'transparent' }}>
                            <img className='furniture4' src={this.state.forthImage} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                    </div>
                    
                    <div style={{ width: '20.5%', height: '61.5%', left: '61.6%', top: '17%', position: 'absolute', zIndex: this.state.currentSelectIndex === 4 ? '2' : '1' }}>
                        <img className='undrag' alt='' src={floorMenu} style={{ width: '100%', left: '0%', top: '0%', position: 'absolute' }}></img>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '15%', top: '13%', background: 'transparent'}}>
                            <img className='floor blue' src={floorBlueBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }}></img>
                        </div>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '15%', top: '41%', background: 'transparent' }}>
                            <img className='floor gray' src={floorGrayBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }}></img>
                        </div>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '15%', top: '70%', background: 'transparent' }}>
                            <img className='floor marble' src={floorMarbleBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '52%', top: '13%', background: 'transparent' }}>
                            <img className='floor tile' src={floorTileBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '52%', top: '41%', background: 'transparent' }}>
                            <img className='floor wood' src={floorWoodBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                        <div style={{ position: 'absolute', width: '35%', height: '18%', left: '52%', top: '70%', background: 'transparent' }}>
                            <img className='floor none' src={floorNoneBtn} style={{ position: 'absolute', width: '100%', height: '100%', objectFit: 'contain' }} ></img>
                        </div>
                    </div>

                    <img className='undrag' alt='' src={ this.state.currentSelectIndex === 0 ? bathroomSelectedBtn : bathroomBtn} style={{ width: '9.5%', right: '6%', top: '24.5%', position: 'absolute' }} onClick={() => { this.menuBtnClick(0); }}/>
                    <img className='undrag' alt='' src={ this.state.currentSelectIndex === 1 ? livingroomSelectedBtn : livingroomBtn} style={{ width: '9.5%', right: '6%', top: '34.5%', position: 'absolute' }} onClick={() => { this.menuBtnClick(1); }}/>
                    <img className='undrag' alt='' src={ this.state.currentSelectIndex === 2 ? bedroomSelectedBtn : bedroomBtn} style={{ width: '9.5%', right: '6%', top: '44.5%', position: 'absolute' }} onClick={() => { this.menuBtnClick(2); }}/>
                    <img className='undrag' alt='' src={ this.state.currentSelectIndex === 3 ? kitchenSelectedBtn : kitchenBtn} style={{ width: '9.5%', right: '6%', top: '54.5%', position: 'absolute' }} onClick={() => { this.menuBtnClick(3); }}/>
                    <img className='undrag' alt='' src={ this.state.currentSelectIndex === 4 ? floorSelectedBtn : floorBtn} style={{ width: '9.5%', right: '6%', top: '64.5%', position: 'absolute' }} onClick={() => { this.menuBtnClick(4); }}/>
                    <img className='press-btn' alt='' src={completeBtn} style={{ height: '8.5%', bottom: '7.5%', right: '15%', position: 'absolute', background: 'transparent' }} onClick={() => this.createScreenshot(this.goToResultPage)}/>
                </div>
            </div>
        )
    }
}

export default withRouter(Game3);