import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import fetchTimeout from 'fetch-timeout';
import backgroundImage from '../assets/images/result/bg.jpg'
import Cookies from 'universal-cookie';
import domtoimage from 'dom-to-image-improved';
import { saveAs } from 'file-saver';

import downloadResultBtn from '../assets/images/result/download_result.png'
import downloadDesignBtn from '../assets/images/result/download_design.png'
import introBtn from '../assets/images/result/intro.png'

import constant from '../assets/const.json';

const cookies = new Cookies();
const APP_NAME_PREFIX = "Building_Engineering_";

class Result extends Component {

    btnSound = './sounds/button_sound.mp3';
    winSound = './sounds/result_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            image: this.props.location.state === undefined ? null : this.props.location.state.image,
            score1: this.props.location.state === undefined ? 0 : this.props.location.state.gameScore1,
            score2: this.props.location.state === undefined ? 0 : this.props.location.state.gameScore2
        };
    }

    componentDidMount() {
        this.playResultSound();
        let uid = cookies.get('uid');
        let score1 = this.state.score1;
        let score2 = this.state.score2;
        console.log('[Params] get uid from cookie = ' + uid);
        if (uid === null || uid === undefined) {
            uid = '';
            console.log('[Params] generate random uuid : ' + uid);
        }
        console.log('[Params] building_score1 = ' + score1);
        console.log('[Params] building_score2 = ' + score2);
        this.postDataToServer(uid, score1, score2);
    }

    getRequestBody(uid, score1, score2) {
        return {
            uid: uid,
            building_score_1: score1,
            building_score_2: score2,
            action: 'result'
        };
    }

    postDataToServer(uid, score1, score2) {
        let apiPath = constant.WEB_API_PATH;
		fetchTimeout(apiPath, {
            headers: {
                'Content-Type': 'application/json'
            },
			method: 'POST',
			body: JSON.stringify(this.getRequestBody(uid, score1, score2))
        }, 5 * 1000, '[WebAPI] Timeout')
        .then(response => response.json())
		.then(data => {
			if (data.success) {
                // call api ok
                console.log('[WebAPI] success');
            } else {
                // call api failed
                console.log('[WebAPI] failed');
            }
		}).catch(e => {
			console.log('[WebAPI] ' + e);
		});
    }

    goToIntroPage() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './intro',
            state: {}
        });
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    playResultSound() {
        let btnAudio = new Audio(this.winSound);
        btnAudio.play();
    }

    downloadDesignImage() {
        this.playBtnSound();
        const date = new Date();
        const mm = date.getMonth() + 1;
        const dd = date.getDate()
        const month = (mm > 9 ? '' : '0') + mm
        const day = (dd > 9 ? '' : '0') + dd
        const year = date.getFullYear();
        saveAs(this.state.image, APP_NAME_PREFIX + year + month + day + '.png');
    }

    downloadResultImage() {
        this.playBtnSound();
        let element = document.getElementById('result-div');
        let scale = constant.DESIRED_RATIO_WIDTH / element.clientWidth;
        domtoimage.toBlob(element, {
            width: constant.DESIRED_RATIO_WIDTH,
            height: constant.DESIRED_RATIO_HEIGHT,
            style: {
                transform: 'scale(' + scale + ')',
                transformOrigin: 'top left'
            }
        })
        .then(function (blob) {
            const date = new Date();
            const mm = date.getMonth() + 1;
            const dd = date.getDate()
            const month = (mm > 9 ? '' : '0') + mm
            const day = (dd > 9 ? '' : '0') + dd
            const year = date.getFullYear();
            saveAs(blob, APP_NAME_PREFIX + year + month + day + '.png');
        });
    }

    render() {
        return (
            <div className='page-root-div'>
                <div id='result-div' style={{ position: 'absolute', width: '100%', height: '100%', left: '0px', top: '0px' }}>
                    <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                    <img id='result-image' alt='' src={this.state.image} style={{ width: '47.5%', height: '80%', left: '4.6%', top: '10%', position: 'absolute', objectFit: 'contain' }}></img>
                    <div style={{ position: 'absolute', right: '12%', top: '46.5%', width: '8%', height: '15%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#152b86" >{this.state.score1}</text>
                        </svg>
                    </div>
                    <div style={{ position: 'absolute', right: '12%', top: '56%', width: '8%', height: '15%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#152b86" >{this.state.score2 }</text>
                        </svg>
                    </div>
                    <div style={{ position: 'absolute', right: '13.5%', top: '67.5%', width: '8%', height: '15%', color: 'white' }}>
                        <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                            <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#e45525" >{(this.state.score1 + this.state.score2)}</text>
                        </svg>
                    </div>
                </div>
                <img className='press-btn' alt='' src={downloadResultBtn} style={{ height: '8%', bottom: '6%', right: '35%', position: 'absolute', background: 'transparent' }} onClick={this.downloadResultImage.bind(this)}/>
                <img className='press-btn' alt='' src={downloadDesignBtn} style={{ height: '8%', bottom: '6%', right: '20%', position: 'absolute', background: 'transparent' }} onClick={this.downloadDesignImage.bind(this)}/>
                <img className='press-btn' alt='' src={introBtn} style={{ height: '8%', bottom: '6%', right: '5%', position: 'absolute', background: 'transparent' }} onClick={this.goToIntroPage.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Result);
