import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/game1/result_bg.jpg';
import nextBtn from '../assets/images/next.png';

class Game1Result extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            score: this.props.location.state === undefined ? 0 : this.props.location.state.gameScore1
        };
    }

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    next() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './desc2',
            state: {
                gameScore1: this.props.location.state === undefined ? 0 : this.props.location.state.gameScore1
            }
        });
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                <div style={{ position: 'absolute', left: '47.1%', bottom: '26%', width: '11%', height: '8%', color: '#086940' }}>
                    <svg width='100%' height='100%' viewBox='0 0 500 75' preserveAspectRatio="xMinYMid meet" style={{ position: 'absolute' }} xmlns="http://www.w3.org/2000/svg" xmlnsXlink='http://www.w3.org/1999/xlink'>
                        <text x="50%" y ="50%" dominantBaseline="middle" textAnchor="middle" fontSize="200" fill="#2e3190" >{this.state.score}</text>
                    </svg>
                </div>
                <img className='press-btn' alt='' src={nextBtn} style={{ height: '10.5%', bottom: '5%', right: '42%', position: 'absolute', background: 'transparent' }} onClick={this.next.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Game1Result);