import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage1 from '../assets/images/desc2/desc1.jpg'
import backgroundImage2 from '../assets/images/desc2/desc2.jpg'
import backgroundImage3 from '../assets/images/desc2/desc3.jpg'
import backgroundImage4 from '../assets/images/desc2/desc4.jpg'
import backgroundImage5 from '../assets/images/desc2/card_desc1.jpg'
import backgroundImage6 from '../assets/images/desc2/card_desc2.jpg'
import backgroundImage7 from '../assets/images/desc2/card_desc3.jpg'
import continueBtn from '../assets/images/continue.png';
import startBtn from '../assets/images/start.png';
import nextBtn from '../assets/images/next_page.png';
import previousBtn from '../assets/images/previous_page.png';

class Desc2 extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
        this.state = {
            index: 0,
            backgroundImage: backgroundImage1
        }
    }

    previous() {
        this.playBtnSound();
        if (this.state.index === 2) {
            this.setState({
                index: 1,
                backgroundImage: backgroundImage2
            });
        } else if (this.state.index === 3) {
            this.setState({
                index: 2,
                backgroundImage: backgroundImage3
            });
        } else if (this.state.index === 4) {
            this.setState({
                index: 3,
                backgroundImage: backgroundImage4
            });
        } else if (this.state.index === 5) {
            this.setState({
                index: 4,
                backgroundImage: backgroundImage5
            });
        } else if (this.state.index === 6) {
            this.setState({
                index: 5,
                backgroundImage: backgroundImage6
            });
        } else if (this.state.index === 7) {
            this.setState({
                index: 6,
                backgroundImage: backgroundImage7
            });
        }
    }

    next() {
        if (this.state.index === 0) {
            this.playBtnSound();
            this.setState({
                index: 1,
                backgroundImage: backgroundImage2
            });
        } else if (this.state.index === 1) {
            this.playBtnSound();
            this.setState({
                index: 2,
                backgroundImage: backgroundImage3
            });
        } else if (this.state.index === 2) {
            this.playBtnSound();
            this.setState({
                index: 3,
                backgroundImage: backgroundImage4
            });
        } else if (this.state.index === 3) {
            this.playBtnSound();
            this.setState({
                index: 4,
                backgroundImage: backgroundImage5
            });
        } else if (this.state.index === 4) {
            this.playBtnSound();
            this.setState({
                index: 5,
                backgroundImage: backgroundImage6
            });
        } else if (this.state.index === 5) {
            this.playBtnSound();
            this.setState({
                index: 6,
                backgroundImage: backgroundImage7
            });
        } else {
            this.goToGame2();
        }
    }
    
    goToGame2() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './game2',
            state: {
                gameScore1: this.props.location.state.gameScore1
            }
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={this.state.backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {
                    this.state.index === 0 && (<img className='press-btn' alt='' src={continueBtn} style={{ height: '9.5%', bottom: '13%', left: '42%', position: 'absolute', background: 'transparent' }} onClick={this.next.bind(this)}/>)
                }
                {
                    (this.state.index > 1 && this.state.index !== 4) && (<img className='press-btn' alt='' src={previousBtn} style={{height: '8.5%', bottom: '9%', left: '12.5%', position: 'absolute', background: 'transparent' }} onClick={this.previous.bind(this)}></img>)
                }
                {
                    this.state.index !== 0 && (<img className='press-btn' alt='' src={(this.state.index === 3 || this.state.index === 6)  ? startBtn : nextBtn} style={{ height: '8.5%', bottom: '9%', right: '14.5%', position: 'absolute', background: 'transparent' }} onClick={this.next.bind(this)}/>)
                }
            </div>
        )
    }
}

export default withRouter(Desc2);