import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import backgroundImage from '../assets/images/desc1_1/bg.jpg'
import nextBtn from '../assets/images/continue.png';

class Desc1_1 extends Component {

    btnSound = './sounds/button_sound.mp3';

    constructor(props) {
        super(props);
    }
    
    goToDesc1_2Page() {
        this.playBtnSound();
        this.props.history.push({
            pathname: './desc1_2',
            state: {}
        });
    };

    playBtnSound() {
        let btnAudio = new Audio(this.btnSound);
        btnAudio.play();
    }

    render() {
        return (
            <div className='page-root-div'>
                <img alt='' src={backgroundImage} style={{ width: '100%', height: '100%', overflow: 'hidden', objectFit: 'cover', position: 'absolute' }}/>
                {/* <img alt='' src={introImage} style={{ width: '60%', position: 'absolute', left: '2.5%', top: '4%' }}></img> */}
                <img className='press-btn' alt='' src={nextBtn} style={{ height: '9.5%', bottom: '13%', left: '42%', position: 'absolute', background: 'transparent' }} onClick={this.goToDesc1_2Page.bind(this)}/>
            </div>
        )
    }
}

export default withRouter(Desc1_1);